<template>
	<view class="content">
		<customHeader :title="pageName" :showBack="true" :headHeight="headerHeight"></customHeader>
		<view class="cont" :style="{marginTop:marginTop+'px'}">
			<view class="top_area" :style="{top:marginTop+'px'}">
				<view class="top">
					<view class="inner" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">{{info.project_name}}</view>
				</view>
				<view class="top_bot">
					<view class="base_info" :class="showMoreInfo?'showall':''">
						<view class="line flex flex-start" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">
							<view class="tit">项目编号：</view>
							<view class="text">{{info.project_code}}</view>
						</view>
						<view class="line flex flex-start" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">
							<view class="tit">项目来源：</view>
							<view class="text">{{info.parent_broker_company_name}}</view>
						</view>
						<view class="line flex flex-start" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">
							<view class="tit">联系人：</view>
							<view class="text">{{info.contact_name}}</view>
						</view>
						<view class="line flex flex-start" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">
							<view class="tit">联系电话：</view>
							<view class="text">{{info.contact_mobile}}</view>
						</view>
						<view class="line flex flex-start" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">
							<view class="tit">我招聘了：</view>
							<view class="text flex flex_btween">
								<view class="num">{{info.worker_count}}</view>
								<view class="period">人</view>
							</view>
						</view>
						<view class="line flex flex-start" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">
							<view class="tit">总工作时长：</view>
							<view class="text flex flex_btween">
								<view class="num">{{info.total_working_time}}</view>
								<view class="period">小时</view>
							</view>
						</view>
						<view class="line flex flex-start" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">
							<view class="tit">接单时间：</view>
							<view class="text">{{info.create_time}}</view>
						</view>
					</view>
					<view class="showmore" @click="showMore">
						<view class="text">{{moreText}}</view>
						<image :src="imgUrl+'/broker/ic_more.png'" mode="widthFix"></image>
					</view>
				</view>
				<view class="tabs flex flex-start">
					<view class="tab" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}" v-for="(item,index) in tabs" :key="index" :class="currentTab.value == item.value?'active':''" @click="changeTab(item)">{{item.text}}</view>
				</view>
			</view>
			<view class="box" :style="{top:boxTop+'px',background:list.length==0?'#fff':'#F3F3F5'}">
				<scroll-view :scroll-top="scrollTop" scroll-y="true" class="scroll-Y" :style="{height:scrollHeight+'px'}"
					@scrolltolower="lower">
					<view class="item" v-for="(item,index) in list" :key="index" v-if="currentTab.value == 'expenditure'" @click="toDetail(item.id)">
						<view class="line flex flex_around">
							<view class="line_inner flex flex-start">
								<view class="tit">工人人数：</view>
								<view class="text bold">{{item.worker_count}}<text class="period">人</text></view>
							</view>
							<view class="line_inner flex flex_end">
								<view class="tit">本期工作时长：</view>
								<view class="text bold">{{item.total_working_time}}<text class="period">小时</text></view>
							</view>
						</view>
						<view class="line flex flex_around">
							<view class="line_inner flex flex-start">
								<view class="tit">本期佣金：</view>
								<view class="text bold">{{item.commission_amount}}<text class="period">元</text></view>
							</view>
							<view class="line_inner flex flex_end">
								<view class="tit">佣金单价：</view>
								<view class="text"><text class="bold blue">{{item.commission_unit_price}}</text><text class="period">/人/时</text></view>
							</view>
						</view>
						<view class="line flex flex-start">
							<view class="tit">佣金去向：</view>
							<view class="text"><text class="bold blue">{{item.sub_broker_company_name}}</text></view>
						</view>
						<view class="line flex flex-start">
							<view class="tit">本期开始时间：</view>
							<view class="text">{{item.project_salary_batch_start_time}}</view>
						</view>
						<view class="line flex flex-start">
							<view class="tit">本期结束时间：</view>
							<view class="text">{{item.project_salary_batch_end_time}}</view>
						</view>
					</view>
					<view class="item" v-for="(item,index) in list" :key="index" v-if="currentTab.value == 'income'" @click="toDetail(item.id)">
						<view class="line flex flex_around">
							<view class="line_inner flex flex-start">
								<view class="tit">工人人数：</view>
								<view class="text bold">{{item.worker_count}}<text class="period">人</text></view>
							</view>
							<view class="line_inner flex flex_end">
								<view class="tit">本期工作时长：</view>
								<view class="text bold">{{item.total_working_time}}<text class="period">小时</text></view>
							</view>
						</view>
						<view class="line flex flex_around">
							<view class="line_inner flex flex-start">
								<view class="tit">本期佣金：</view>
								<view class="text bold">{{item.commission_amount}}<text class="period">元</text></view>
							</view>
							<view class="line_inner flex flex_end">
								<view class="tit">佣金单价：</view>
								<view class="text"><text class="bold blue">{{item.commission_unit_price}}</text><text class="period">/人/时</text></view>
							</view>
						</view>
						<view class="line flex flex-start">
							<view class="tit">本期开始时间：</view>
							<view class="text">{{item.project_salary_batch_start_time}}</view>
						</view>
						<view class="line flex flex-start">
							<view class="tit">本期结束时间：</view>
							<view class="text">{{item.project_salary_batch_end_time}}</view>
						</view>
					</view>
					<view class="empty" v-if="list.length == 0">
						<image :src="imgUrl+'/broker/empty.png'" mode="heightFix"></image>
					</view>
				</scroll-view>
			</view>
		</view>
	</view>
</template>

<script>
	import tabbar from "@/components/tabbar.vue"
	import customHeader from "@/components/custom_header.vue"
	const app = getApp()
	export default {
		data() {
			return {
				imgUrl:app.globalData.baseImageUrl,
				currentTab:{
					value:"expenditure",
					text:"支出记录",
				},
				pageName:"佣金详情",
				marginTop:app.globalData.marginTop,
				tabMargin:app.globalData.tabMargin,
				subTabHeight:app.globalData.subTabHeight,
				comValue:"",
				startTime:"",
				endTime:"",
				currentStatus:0,//0：进行中，1：已结束
				comRange:[
					{
						value:0,
						text:"成都有职劳务有限公司"
					},
					{
						value:1,
						text:"成都顶呱呱劳务有限公司"
					}
				],
				tabs:[
					{
						value:"expenditure",
						text:"支出记录",
					},
					{
						value:"income",
						text:"收入记录",
					}
				],
				info:{
					id:"",
					project_name:"",
					broker_name:"",
					contact_mobile:"",
					contact_name:""
				},
				scrollHeight:0,
				boxTop:0,
				headerHeight:app.globalData.topHeight,
				maskInnerTop:0,
				showMoreInfo:false,
				moreText:"展开",
				id:"",
				list:[],
				pagination:{},
				currentPage:1
			}
		},
		components:{
			tabbar,customHeader
		},
		onPullDownRefresh() {
			this.currentPage = 1
			this.list = []
			this.currentTab = {
					value:"expenditure",
					text:"支出记录",
			}
			this.getList()
			this.getInfo()
		},
		onLoad(param) {
			this.id = param.id
			let _this = this
			this.getMarginTop()
			setTimeout(function(){
				_this.getElementInfo()
				_this.getTopHeight()
			},500)
			this.getInfo()
			this.getList()
		},
		methods: {
			getMarginTop(e){
				let menuButtonInfo = uni.getMenuButtonBoundingClientRect()
				this.marginTop = menuButtonInfo.height + menuButtonInfo.top + 10
				this.maskInnerTop = this.marginTop + 20
			},
			getTopHeight(){
				let percent = 200/844
				let systemInfo = uni.getSystemInfoSync()
				this.headerHeight =Math.floor(systemInfo.screenHeight * percent) 
			},
			getElementInfo(){
				let _this = this
				let topHeight=0;
				let systemInfo = uni.getSystemInfoSync()
				let tabbarHeight =Math.floor(systemInfo.screenHeight * (46/844)) 
				console.log(tabbarHeight,"tabbar")
				let info = uni.createSelectorQuery().select(".top_area");
				info.boundingClientRect(function(data) { //data - 各种参数
				   topHeight = data.bottom - data.top  // 获取元素高度
				   _this.boxTop = _this.marginTop + data.height
				   _this.scrollHeight = systemInfo.screenHeight - _this.boxTop - tabbarHeight - 5
				}).exec()
				
			},
			changeTab(item){
				this.currentTab.value = item.value
				this.currentTab.text = item.text
				this.list = []
				this.getList()
			},
			showMore(){
				let _this = this
				this.showMoreInfo = !this.showMoreInfo
				if(this.showMoreInfo){
					this.moreText = "收起"
				}else{
					this.moreText = "展开"
				}
				setTimeout(function(){
					_this.getMarginTop()
					_this.getElementInfo()
					_this.getTopHeight()
				})
				
			},
			getInfo(){
				let url = "/broker/mini/project-broker/"+this.id+"/commission/detail"
				this.$request(url).then(res => {
					if(res.code == 0){
						this.info = res.data
					}else{
						uni.showModal({
							title:res.msg
						})
					}
				})
			},
			getList(){
				let url = "/broker/mini/project-broker/"+this.id+"/commission/"+this.currentTab.value+"?page="+this.currentPage
				this.$request(url).then(res => {
					if(res.code == 0){
						this.list = this.list.concat(res.data.list)
						this.pagination = res.data.pagination
					}else{
						uni.showModal({
							title:res.msg
						})
					}
				})
			},
			toDetail(id){
				uni.navigateTo({
					url:"/subpkg/commission_worker_detail/commission_worker_detail?id="+id+"&type="+this.currentTab.value
				})
			},
			lower(){
				if(this.list.length < this.pagination.totalCount){
					this.currentPage++
					this.getList()
				}else{
					if(this.list.length >= this.pagination.totalCount){
						uni.showToast({
							title:"已经到底啦~",
							icon:"none",
							duration:2000
						})
					}
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
.top_area{
	width:calc(100% - 96rpx);
	position: fixed;
	left:48rpx;
	font-size: 0;
	&>image{
		width:100%;
	}
	.top{
		background: url($back-ground-url+"/broker/ic_commission_title_bg.png") no-repeat;
		background-size: 100% 100%;
		height:80rpx;
		line-height: 80rpx;
		padding:4rpx 40rpx;
		.inner{
			border-bottom: 1px dashed #979797;
			font-size: 30rpx;
			color:#333333;
			position: relative;
			padding-left:20rpx;
			font-weight: bold;
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
			&:before{
				content:"";
				width:8rpx;
				height:28rpx;
				background: #F7BC05;
				border-radius: 2rpx;
				position: absolute;
				top:50%;
				left:0;
				transform: translateY(-50%);
			}
		}
	}
	.line{
		height:88rpx;
		line-height: 88rpx;
		font-size: 28rpx;
		border-bottom: 1px dashed #DFDFDF;
		&:last-child{
			border-bottom: none;
		}
		.tit{
			color: #5A5A5A;
			width: 175rpx;
			
		}
		.text{
			width: calc(100% - 175rpx);
			color:#333;
			.period{
				font-size: 24rpx;
				color: #A8A8A8;
			}
		}
	}
	.top_bot{
		background: url($back-ground-url+"/broker/ic_commission_detail_bg.png") no-repeat;
		background-size:cover;
		padding-bottom: 30rpx;
		.base_info{
			padding:0 40rpx;
			box-sizing: border-box;
			height:88rpx;
			overflow: hidden;
			.line{
				margin-top:0;
			}
			&.showall{
				height:auto;
				overflow: visible;
			}
		}
		.showmore{
			font-size: 24rpx;
			color:#F7BC05;
			text-align: center;
			.text{
				display: inline-block;
			}
			image{
				width:30rpx;
				height:30rpx;
				vertical-align: middle;
			}
		}
	}
	.tabs{
		background: #FFFFFF;
		box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.15);
		border-radius: 8px;
		position: relative;
		margin:20rpx 0;
		&::after{
			content:"";
			width:2rpx;
			height:30rpx;
			background: #DFDFDF;
			position: relative;
			top:50%;
			left:50%;
			transform: translate(-50%,-50%);
		}
		.tab{
			width:50%;
			font-size: 30rpx;
			color:#333;
			position: relative;
			text-align: center;
			&.active{
				font-weight: bold;
				color: #F7BC05;
			}
			&.active::after{
				content:"";
				width:28rpx;
				height:10rpx;
				background: url($back-ground-url+"/broker/arrow_down_yellow.png") no-repeat;
				background-size: 100% 100%;
				position: absolute;
				bottom:8rpx;
				left:50%;
				transform: translateX(-50%);
			}
		}
	}
}
.box{
	position: fixed;
	width: calc(100% - 96rpx);
	left:48rpx;
	z-index:3;
	overflow: hidden;
	background-size:100% 100%;
	box-sizing: border-box;
	.item{
		margin-bottom:20rpx;
		background: #FFFFFF;
		border-radius:16rpx;
		padding:40rpx;
		box-sizing: border-box;
		.line{
			margin-bottom: 24rpx;
			font-size: 28rpx;
			&:last-child{
				margin-bottom: 0;
			}
			.line_inner{
				width: 50%;
				&:last-child{
					text-align: right;
				}
			}
			.tit{
				color:#686868;
			}
			.text{
				color:#333;
			}
			.bold{
				font-weight: bold;
			}
			.blue{
				color:#4284F4;
			}
			.period{
				font-size: 24rpx;
				color:#A8A8A8;
				font-weight: normal;
				margin-left: 8rpx;
			}
		}
	}
}
</style>
